import styled from '@emotion/styled';
import React, { FC, useEffect, useState } from 'react';
import Helmet from 'react-helmet';
import { useTranslation } from 'react-i18next';

import { initGA } from '../../../initGA';
import Header from '../../../ui/Header';
import { NEW_FEATURES } from '../../../../../feature-toggle';


const Container = styled('div')`
  height: 100%;
  background: #FFFFFF;
`;

const SectionContainer = styled('section')`
  width: 100%;
  padding: 0 15px;
  background-color: #FFFFFF;
  font-size: 1rem;
  color: #212529;
`;

const MainContainer = styled('div')`
  max-width: 1140px;
  height: 100%;
  margin: 0 auto;
  text-align: center;
`;

const MainTitle = styled('h1')`
  font-family:Rubik, sans-serif;
  font-style:normal;
  font-size: 31px;
  font-weight: 700;
  margin-top: 31px;
  margin-bottom: 8px;
  color: #24292E;
  line-height: 1.25;
`;

const MainDescription = styled('p')`
  color: #233742;
  font-family:Rubik, sans-serif;;
  font-style:normal;
  font-size: 18px;
  font-weight: 400;
  margin-top: 23px;
  margin-bottom: 25px;
  line-height: 146%;
  text-align: center;
`;

const LinkTryAgain = styled('a')`
  color: #000000;
  display: inline-block;
  text-decoration: underline;
  font-family:Rubik, sans-serif;;
  font-style:normal;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.46;
`;

const GratitudeSubtitle = styled(MainDescription)`
  font-weight: bold;
  margin: 0 0 100px 0;
`;

const SectionPreview = styled('section')`
  background: #F2F5F8;
`;

const PreviewContainer = styled(MainContainer)`
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 60px;
`;

const PreviewScreenshotsContainer = styled('div')`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto -15px;
`;

const PreviewScreenshotsWrapper = styled('div')`
  position: relative;
  height: 800px;
  flex-basis: 100%;
  flex-grow: 0;
  flex-shrink: 0;
  margin-bottom: 30px;
  max-width: 100%;
  min-height: 1px;
  padding: auto 15px;
`;

const PreviewImage = styled('img')`
  position: absolute;
  vertical-align: middle;
  border-style: none;
  opacity: 1;
  transition: opacity .3s;
  max-width: 100%;
  text-align: center;

  &:nth-child(1){
    left: -1%;
    bottom: 21.5%;
    z-index: 1;
  }
  &:nth-child(2){
     left: 15.8%;
     top: -78px;
     z-index: 5;
  }
  &:nth-child(3){
    left: 28.3%;
     bottom: 1%;
     z-index: 10;
  }
`;

const OsContainer = styled('div')`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto -15px;
`;

const OsWrapper = styled('div')`
  display: flex;
  flex: 0 0 83.3%;
  max-width: 83.3%;
  position: relative;
  width: 100%;
  min-height: 1px;
  padding: auto 15px;
  justify-content: space-around;
`;

const OsBlock = styled('div')`
  text-align: center;
  width: 100%;
  &:not(:last-child) {
    background-image: url('https://lcdn.gologin.com/img/iconDownload/line.svg');
    background-repeat: no-repeat;
    background-position: 100% 0;
  }
`;

const ImgOs = styled('img')`
  vertical-align: middle;
  border-style: none;
  opacity: 1;
  transition: opacity .3s;
  max-width: 100%;
`;

const DownloadBtn = styled('a')`
  background: #424F7B;
  border-radius: 3px;
  font-family: Rubik, sans-serif;
  padding: 9px 24.5px;
  font-weight: 500;
  font-size: 15px;
  line-height: 146.1%;
  text-align: center;
  color: #FFFFFF !important;
  text-decoration: none;
  user-select: none;
  cursor: pointer;
`;

const GooglePlayImage = styled('img')`
  width: 120px;
  height: 36px;
  vertical-align: middle;
  border-style: none;
`;

const arrayOSs: any = [
  {
    name: 'Windows',
    downloadLink: 'https://dl.gologin.com/gologin.exe',
    osImg: 'https://lcdn.gologin.com/img/iconDownload/win.svg'
  },
  {
    name: 'Linux',
    downloadLink: 'https://dl.gologin.com/gologin.tar',
    osImg: 'https://lcdn.gologin.com/img/iconDownload/lin.svg'
  },
  {
    name: 'Mac',
    downloadLink: 'https://dl.gologin.com/gologin.dmg',
    osImg: 'https://lcdn.gologin.com/img/iconDownload/mac.svg'
  },
  {
    name: 'Android',
    downloadLink: 'https://play.google.com/store/apps/details?id=com.gologin.gologin_mobile',
    osImg: 'https://lcdn.gologin.com/img/qrcode.png',
    googlePlayImg: 'https://lcdn.gologin.com/img/PlayMarket.svg'
  }
];

const previewImgLinks =  [
  'https://lcdn.gologin.com/img/screens/gologin_fs2.png',
  'https://lcdn.gologin.com/img/screens/gologin_fs3.png',
  'https://lcdn.gologin.com/img/screens/gologin_fs1.png',
];

const previewImgRender = () => {
  const previewArr = [];

  for (const item of previewImgLinks) {
    const temp = <PreviewImage src={item} alt='Profiles Page'/>;
    previewArr.push(temp);
  }

  return previewArr;
};

const DownloadPage: FC = () => {
  const [downloadLink, setDownloadLink] = useState<string>('');
  const [clientId, setClientId] = useState<string>('');

  const { t: translation } = useTranslation();

  useEffect(() => {
    autoDownloadApp();
  }, []);

  const getOsVersion = (): 'Windows'|'Mac'|'Linux' => {
    const appVersion = navigator.appVersion;
    let os: 'Linux'|'Windows'|'Mac' = 'Windows';

    switch (true) {
      case appVersion.includes('Mac'):
        os = 'Mac';
        break;
      case appVersion.includes('X11') || appVersion.includes('Linux'):
        os = 'Linux';
        break;
      default:
    }

    return os;
  };

  const generateLinkForDownloadApp = async (): Promise<string> => {
    const os = getOsVersion();
    const googleClientId: string = await initGA();
    setClientId(googleClientId);
    const [windowsPathObject] = arrayOSs;

    for (const item of arrayOSs) {
      if (item.name === os) {
        const updatedDownloadLink = `${item.downloadLink}?googleClientId=${googleClientId}`;
        setDownloadLink(updatedDownloadLink);
        return updatedDownloadLink;
      }
    }

    return windowsPathObject.downloadLink;
  };

  const autoDownloadApp = async () => {
    await new Promise(r => setTimeout(r, 500));
    const link: string = await generateLinkForDownloadApp();
    window.location.href = link;
  };

  const osBlockRender = () => {
    const completeArr = [];

    for (const item of arrayOSs) {
      if (item.name !== 'Android') {
        completeArr.push(
          <OsBlock>
            <div>
              <ImgOs src={item.osImg}/>
            </div>
            <div style={{marginTop: 34}}>
              <DownloadBtn href={`${item.downloadLink}?googleClientId=${clientId}`}>{translation('downloadApp.download')}</DownloadBtn>
            </div>
          </OsBlock>
        );
        continue;
      }

      completeArr.push(
        <OsBlock>
          <div>
            <ImgOs src={item.osImg} style={{ width: 62 }}/>
          </div>
          <div style={{ marginTop: 28 }}>
            <a href={item.downloadLink}>
              <GooglePlayImage src={item.googlePlayImg} alt={'PlayMarket link'}/>
            </a>
          </div>
        </OsBlock>
      );
    }

    return completeArr;
  };

  return (
    <>
      <Helmet
        title='Download App - GoLogin'
      />
      <Container>
        <Header />
        <SectionContainer>
          <MainContainer>
            <MainTitle>
              {translation('downloadApp.title')}{getOsVersion()}...
            </MainTitle>
            <MainDescription>
              {translation('downloadApp.subtitle')}
              <br/>
              {translation('downloadApp.dontStart')}<LinkTryAgain href={downloadLink}>{translation('base.tryAgain')}</LinkTryAgain>
            </MainDescription>
            <GratitudeSubtitle>
              {translation('downloadApp.thanks')}
            </GratitudeSubtitle>
          </MainContainer>
        </SectionContainer>
        <SectionPreview>
          <PreviewContainer>
            <PreviewScreenshotsContainer>
              <PreviewScreenshotsWrapper>
                {previewImgRender()}
              </PreviewScreenshotsWrapper>
            </PreviewScreenshotsContainer>
            <OsContainer>
              <OsWrapper>
                {osBlockRender()}
              </OsWrapper>
            </OsContainer>
          </PreviewContainer>
        </SectionPreview>
      </Container>
    </>
  );
};

export default DownloadPage;
